<template>
  <div class="exam-page">
    <van-form @submit="onSubmit">
      <div class="question" v-for="(question, index) in questionList" :key="question.id">
        <div class="title">{{ index + 1 + ". " + question.title }}</div>
        <div class="title">{{ "id: " + question.id }}</div>
        <van-field name="radio" label="" :rules="[{ required: true, message: '' }]">
          <template #input>
            <van-radio-group v-model="question.selected" :disabled="isSubmit">
              <van-radio
                v-for="option in question.options"
                :key="option.name"
                :name="option.name"
                :class="{
                  'selected-option': isSubmit && option.name === question.selected,
                  'answer-option': isSubmit && option.name === question.answer
                }"
                >{{ option.name + ". " + option.label }}</van-radio
              >
            </van-radio-group>
          </template>
        </van-field>
      </div>
      <div style="margin: 16px;">
        <van-button v-if="!isSubmit" round block type="info" native-type="submit"></van-button>
      </div>
    </van-form>
    <van-button v-if="isSubmit" round block type="warning" native-type="button" @click="init"
      ></van-button
    >
  </div>
</template>

<script>
import cloneDeep from "lodash/cloneDeep";
import shuffle from "lodash/shuffle";
import { questionCollection } from "./questionCollection";
export default {
  name: "Exam",
  data() {
    return {
      questionList: [],
      isSubmit: false
    };
  },
  created() {
    this.init();
  },
  methods: {
    init() {
      const newQuestionCollection = shuffle(cloneDeep(questionCollection));
      this.isSubmit = false;
      this.questionList = newQuestionCollection.slice(0, 5);
    },
    onSubmit() {
      this.isSubmit = true;
      const result = this.questionList.every(question => {
        return question.selected === question.answer;
      });
      if (result) {
        /* TODO */
        this.$dialog.alert({
          title: "",
          message: ""
        })
      } else {
        this.$dialog.alert({
          title: "",
          message: ""
        })
      }
    }
  }
};
</script>

<style lang="less" scoped>
.exam-page {
  padding: 80px 50px 300px;
  background: #fff;
  .question {
    .title {
      font-size: 30px;
    }
    .van-radio {
      margin: 30px 0;
      ::v-deep .van-radio__label {
        font-size: 30px;
      }
    }
    .selected-option {
      ::v-deep .van-radio__label {
        font-weight: 500;
        color: red;
      }
    }
    .answer-option {
      ::v-deep .van-radio__label {
        font-weight: 500;
        color: green;
      }
    }
  }
}
</style>
